<template>
  <div class="classify-lable lightest-bg">
    <div class="right">
      <div class="lable-top">
        <ul>
          <li
            v-for="(item,i) in titleList"
            :key="i"
            @click="jump(item,i);"
           
          >
            <a href="javascript:void(0)">
              <img :src="`https://mhb.hanyujingyun.cn/labels/${item.icon}.svg`">
              <p class="medium-text">{{item.title}}</p>
            </a>
          </li>
        </ul>
      </div>
    </div>
  </div>
</template>
<script>
export default {
  data() {
    return {
      titleList: ""
    };
  },
  mounted() {
    var _this = this;
    var url = this.APIURL + "/classic/classic/all";
    this.$axios.post(url, { channel: 12 }).then(function({ data }) {
      _this.titleList = data.data.labels;
      // console.log(_this.titleList);
    });
  },
  methods: {
    jump(lable, i) {
      if (this.$route.path == "/index") {
        var selectId = i;
        // console.log(selectId);
        localStorage.setItem("toId", selectId);
        this.$router.push("/classic");
        setTimeout(() => {
          localStorage.setItem("toId", "");
        }, 2000);
      } else {
        let jump = document.getElementsByClassName("lable");
        // 获取需要滚动的距离
        let total = jump[i].offsetTop - 320;
        // Chrome
        document.body.scrollTop = total;
        // Firefox
        document.documentElement.scrollTop = total;
        // Safari
        window.pageYOffset = total;
      }
    }
  }
};
</script>
<style lang="scss">
.classify-lable {
  width: 1200px;
  height: 261px;
  margin: 0 auto;
  overflow: hidden;
  padding: 20px 35px;
  border-radius: 6px;
  .right {
    float: left;
    .iconfont {
      font-size: 50px;
    }
    .lable-top {
      width: 100%;
      // border: 1px solid red;
      ul {
        overflow: hidden;
        padding: 0;
        width: 100%;
        margin: 0;
        li {
          float: left;
          margin: 0;
          padding: 0;
          text-align: center;
          margin: 0 31px 0 30px;
          margin-top: 12px;
          width: 80px;
          height: 96px;
          list-style: none;
          a {
            text-decoration: none;
            img {
              width: 50px;
              height: 50px;
            }
          }
        }
      }
      p {
        margin: 0;
        opacity: 1;
        font-size: 16px;
        font-family: Adobe Heiti Std;
        letter-spacing: 0px;
        margin-top: 5px;
      }
    }
   
  }
}
</style>
