<template>
  <div class>
    <LunBo :dataimg="dataimg"></LunBo>

    <labels style="margin-top:28px" />
    <div class="classic">
      <div class="left">
        <div
          class="lable"
          :id="'no' + i"
          v-for="(item, i) in tableList"
          :key="i"
        >
          <div class="top">
            <span class="text1 darkest-text">{{ item.title }}</span>
            <a
              href="javascript:void(0)"
              v-on:click.stop="lookMore(item, i)"
              class="more theme-text"
              >查看更多>></a
            >
          </div>
          <div class="bottom lightest-bg">
            <div
              class="book"
              v-for="(book, index) in (item.list || '').slice(0, 4)"
              async
              :key="index"
            >
              <div class="bookImg">
                <a class="lightest-box">
                  <img
                    :src="book.images.detail"
                    :alt="book.title"
                    @click="goNews(item, i, book)"
                  />
                </a>
              </div>
              <p class="classic-title">{{ book.title }}</p>
              <a class="lightest-box">
                <div class="bookBt" @click="goNews(item, i, book)">
                  立刻收听
                </div>
              </a>
            </div>
          </div>
        </div>
      </div>
      <div class="right" id="advertPlace">
        <div class="inner" :style="{ top: scrollTop + 'px' }">
          <div class="adviertisement-1">
            <!-- <a
              href="https://mp.weixin.qq.com/s?__biz=MzA4MTgwMzg3Mg==&tempkey=MTAxNF9wemQ3MXVrTjgvZDZsanFZWk5FdFhvb0lTZncwLURzbVhCTjBDNy04TGI5azktNFJUcllmR1dRQkhHb1NBS0tIdTdWdUNHM2pHS041TDRyODFkb2p5TzJTLTZDYnpCNEhJSkhWUXNWUVlaZk5FNXktek0yOHU0VEVheEMzSk0xVkhOeXNBejQySG5ncG5WOHU3bVIzSHZBWEtOdVVaQ0p0cTNKR1FBfn4%3D&chksm=079da4c130ea2dd71d3df9baf23a1f72ae5193d977a421c775ccaa4b007003327baa30a29dba&mpshare=1&scene=1&srcid=0621xeRdxEhpqGvQLQqEpDP6#wechat_redirect"
              target="_blank"
            > -->
            <img src="../../assets/img/guanggao1.png" alt />
            <!-- </a> -->
          </div>
          <div class="adviertisement-2">
            <advertisement />
          </div>
          <div class="adviertisement-3">
            <a
              href="https://mp.weixin.qq.com/s/ENqQJn9524x6oSxD7g3tWQ"
              target="_blank"
            >
              <img src="../../assets/img/guanggao2.png" class="imgs" />
            </a>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import labels from "../../components/label";
import LunBo from "../../components/lunbo";
import advertisement from "../../components/advertising-tab";
let app = "";
export default {
  data() {
    var tableList = "";
    let app = this;
    return {
      scroll: "",
      setData: "",
      tableList: "",
      scrollTop: 0,
      dataimg: [
        {
          src: require("../../assets/img/lunbo1.png")
        },
        {
          src: require("../../assets/img/lunbo2.png")
        },
        {
          src: require("../../assets/img/lunbo3.png")
        }
      ]
    };
  },
  created() {
    //  var selectId =localStorage.getItem('toId');
    //   console.log(selectId)
  },
  mounted() {
    var _this = this;
    var url = this.APIURL + "/classic/classic/all";
    this.$axios.post(url, { channel: 12 }).then(function({ data }) {
      _this.tableList = data.data.dataSet;
      // data.data.dataSet.map(val => {
      //   _this.getData(val).then(data => {
      //     val.list = data?data.data:{};
      //     val = Object.assign({}, val);
      //     _this.tableList = [].concat(_this.tableList);
      //   });
      //   return val;
      // });
    });
    _this.$nextTick(function() {
      window.addEventListener("scroll", _this.handleScroll);
    });
    let offsetTop = document.getElementById("advertPlace").offsetTop;
    document.onscroll = function(e) {
      let scrollTop = document.documentElement.scrollTop;
      if (scrollTop > offsetTop) {
        let diff = scrollTop - offsetTop;
        _this.scrollTop = diff;
      } else {
        _this.scrollTop = 0;
      }
    };
  },
  updated: function() {
    this.$nextTick(function() {
      this.getlocat();
    });
  },
  setOffset(val) {
    this.scrollTop = val;
  },
  methods: {
    getlocat() {
      let selectId = localStorage.getItem("toId");
      var noselectId = "no" + selectId;
      // console.log(noselectId);
      if (noselectId !== null) {
        document.getElementById(noselectId);
        // .scrollIntoView(true);
      }
    },
    getData(val) {
      var _this = this;
      var url = this.APIURL + "/classic/classic/classic_list";
      let data = {
        labelId: val.labelId,
        pageSize: 1000
      };
      this.$axios.post(url, data).then(function(res) {
        if (res.status == 200) {
          _this.$store.commit("setBookList", res.data.data);
        }
      });
    },
    lookMore(item) {
      sessionStorage.setItem("ClassicLabel", item.title);
      this.$router.push("/classic/classicmore");
      document.title = item.title + "_" + "李罕诵经";
      this.getData(item);
    },
    // 点击图片跳转经典详情页
    goNews(item, i, book) {
      //点击获取Id值 通过路由跳转传递参数
      var _this = this;
      // _this.setData = book.id;
      this.$store.commit("setClassicId", book.id);
      //  this.$store.commit("setClassicLabel", item);
      localStorage.setItem("ClassicId", book.id);
      sessionStorage.setItem("ClassicLabel", item.title);

      this.$router.push("/classic/details"); //跳转的路由;
      document.title = item.title + "_" + "李罕诵经";
    },

    dataScroll: function() {
      this.scroll =
        document.documentElement.scrollTop || document.body.scrollTop;
    },
    jump(i) {
      let jump = document.getElementsByClassName("lable");
      // 获取需要滚动的距离
      let total = jump[i].offsetTop - 320;
      // Chrome
      document.body.scrollTop = total;
      // Firefox
      document.documentElement.scrollTop = total;
      // Safari
      window.pageYOffset = total;
    }
  },
  computed: {},
  watch: {},
  components: {
    LunBo,
    advertisement,
    labels
  }
};
</script>
<style lang="scss">
.classic {
  width: 1200px;
  margin: 0 auto;
  overflow: hidden;
  display: flex;
  .left {
    .lable {
      width: 919px;
      overflow: hidden;
      margin-top: 16px;
      overflow: hidden;
      .more {
        margin: 14px 24px 0 0;
        float: right;
        opacity: 1;
        font-size: 14px;
        font-family: Adobe Heiti Std;
        letter-spacing: 0px;
        text-decoration: none;
      }
      .bottom {
        overflow: hidden;
        padding: 0 14px;
        .book {
          width: 189px;
          margin: 14px 44px 0 0;
          float: left;
          height: 300px;
          position: relative;
          &:nth-child(4n) {
            margin-right: 0;
          }
          .bookImg {
            width: 100%;
            height: 180px;
            img {
              width: 100%;
              height: 100%;
              opacity: 1;
            }
          }
          .classic-title {
            text-align: center;
            margin-top: 4px;
          }
        }
        a {
          text-decoration: none;
          .bookBt {
            width: 45px;
            height: 45px;
            border: 1px solid;
            font-size: 12px;
            text-align: center;
            padding: 5px;
            border-radius: 50%;
            position: absolute;
            bottom: 10px;
            left: 70px;
          }
          .bookBt:hover {
            color: #fff;
            background: #584945;
          }
        }
      }
    }
  }
  .right {
    margin-left: 14px;
    margin-top: 64px;
    width: 266px;
    height: auto;
    position: relative;
    .inner {
      position: absolute;
      .adviertisement-1 {
        width: 100%;
        height: 176px;
        opacity: 1;
      }
      .adviertisement-2 {
        width: 100%;
        height: 116px;
        opacity: 1;
        background: #fff;
        margin-top: 14px;
      }
      .adviertisement-3 {
        position: fixed;
        width: 100%;
        height: 100%;
        opacity: 1;
        background: #fff;
        position: relative;
        margin-top: 14px;
        .imgs {
          width: 100%;
          height: 640px;
          //   animation: ghostUpdown 50s infinite alternate;
          //  -webkit-animation: ghostUpdown 50s infinite alternate;
        }
      }
    }
  }
}
@keyframes ghostUpdown {
  from {
    top: 0;
  }
  to {
    top: 78%;
  }
}
@-webkit-keyframes ghostUpdown {
  from {
    top: 78%;
  }
  to {
    top: 0;
  }
}
</style>
